//https://cookieconsent.insites.com/documentation/disabling-cookies/
//https://cookieconsent.insites.com/documentation/javascript-api/
window.addEventListener("load", function () {
    //Set up cookie notification on load
    var p;
    cookieconsent.initialise({
        "showLink": true,
        "position": "top",
        "type": "opt-in",
        "container": document.getElementById("cookie-msg"),
        "content": {
            "message": "We would like to use cookies to store information on your computer in order to help us make this website better.",
            "dismiss": "Decline",
            "allow": "Accept",
            "link": "Learn more",
            "href": "https://documents.markerstudygroup.com/media/278/privacy-policy-auto-windscreens-28-6-16.pdf"
        },
        onPopupOpen: function () {
            //console.log('onPopupOpen() called');
        },
        onPopupClose: function () {
            //console.log('onPopupClose() called');
        },
        onInitialise: function (status) {
            //console.log('onInitialise() called with status ' + status + '');
        },
        onStatusChange: function (status) {
            //console.log('onStatusChange() called with status ' + status + '');
        },
        onRevokeChoice: function () {
            //console.log('onRevokeChoice() called');
        },
    },
        function (popup) {
            p = popup;
        },
        function (err) {
            //console.error(err);
        });
    document.getElementById('revoke-cookies').onclick = function (e) {
        //console.log("Calling revokeChoice()");
        p.revokeChoice();
    };
});